<template>
    <div>
        <div>
            <el-input size="small"
                      class="addPostInput"
                      placeholder="添加职位..."
                      prefix-icon="el-icon-plus"
                      @keydown.enter.native="addPosition"
                      v-model="pos.name">
            </el-input>
            <el-button icon="el-icon-plus" size="small" type="primary" @click="addPosition">添加</el-button>
        </div>
        <div class="posManaMain">
            <el-table
                    :data="positions"
                    stripe
                    @selection-change="handleSelectionChange"
                    border
                    size="small"
                    style="width: 70%"> <!--@selection-change自动选中当前你勾选的选项-->
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="id"
                        label="编号"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="职位名称"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="createDate"
                        label="创建时间"
                        width="150">
                </el-table-column>
                <el-table-column
                        label="是否启用"
                width="80">
                    <template slot-scope="scope">
                        <el-tag size="small" type="success" v-if="scope.row.enabled">已启用</el-tag>
                        <el-tag size="small" type="danger" v-else>未启用</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                                size="mini"
                                @click="showEditView(scope.$index, scope.row)">编辑
                        </el-button>
                        <!--scope.row是返回那一行的json-->
                        <el-button
                                size="mini"
                                type="danger"
                                @click="handleDelete(scope.$index, scope.row)">删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-button type="danger" @click="deletePositions" size="small" style="margin-top: 8px"
                       :disabled="multipleSelections.length==0">批量删除
            </el-button>
        </div>
<!--        弹出的编辑框-->
        <el-dialog
                title="修改职位"
                :visible.sync="dialogVisible"
                width="30%">
            <div>
                <el-tag>职位名称</el-tag>
                <el-input class="updatePosition" size="small" v-model="updatePos.name"></el-input>
            </div>
            <div>
                <el-tag>是否启用</el-tag>
                <el-switch
                        v-model="updatePos.enabled"
                        active-text="启用"
                        inactive-text="禁用">
                </el-switch>
            </div>
            <span slot="footer">
    <el-button size="small" @click="dialogVisible = false">取 消</el-button>
    <el-button size="small" type="primary" @click="doUpdate">确 定</el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "PosMana",
        data() {
            return {
                pos: {
                    name: '',
                },
                positions: [],
                updatePos: {
                    name: '',
                    enabled:null
                },
                dialogVisible: false,
                multipleSelections: [],
            }
        },
        mounted() {
            this.initPosition(); //查询这一步会自动加载列表
        },
        methods: {
            handleSelectionChange(val) {
                this.multipleSelections = val;
            },
            deletePositions() {
                this.$confirm('此操作将永久删除【' + this.multipleSelections.length + '】条记录, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let ids = '?';
                    this.multipleSelections.forEach(item => {
                        ids += 'ids=' + item.id + '&';
                    })
                    this.deleteRequest("/system/basic/pos/" + ids).then(resp => {
                        if (resp) {
                            this.initPosition();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    })
                })
            },
            doUpdate() {
                this.putRequest("/system/basic/pos/", this.updatePos).then(resp => {
                    if (resp) {
                        // this.initPosition();
                        this.updatePos.name = '';
                        this.dialogVisible = false;
                    }
                })
            },
            initPosition() {
                this.getRequest("/system/basic/pos/").then(resp => {
                    if (resp) {
                        this.positions = resp;
                        this.pos.name = '';
                    }
                })
            },
            showEditView(index, row) {
                Object.assign(this.updatePos, row);  //解决编辑框跟展示列表一致修改问题 eg: this.updatePos=row;
                this.dialogVisible = true;
            },
            handleDelete(index, row) {
                this.$confirm('此操作将永久删除【' + row.name + '】职位, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/basic/pos/" + row.id).then(resp => {
                        if (resp) {
                            this.initPosition();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    })
                })
            },
            addPosition() {
                if (this.pos.name) {
                    this.postRequest("/system/basic/pos/", this.pos).then(resp => {
                        if (resp) {
                            this.initPosition();
                        }
                    })
                } else {
                    this.$message.error("职位名称不可为空")
                }
            }
        }
    }
</script>

<style>
    .addPostInput {
        width: 300px;
        margin-right: 5px;
    }

    .posManaMain {
        margin-top: 10px;
    }

    .updatePosition {
        width: 200px;
        margin-left: 8px;
    }
</style>