<template>
    <div>
        <div>
            <el-input placeholder="请输入角色英文名称..." v-model="role.name" size="small"
                      style="width: 300px;margin-right: 8px">
                <template slot="prepend">ROLE_</template>
            </el-input>
            <el-input placeholder="请输入角色中文名称..." size="small" style="width: 200px;margin-right: 8px" v-model="role.nameZh"></el-input>
            <el-button type="primary" size="small" icon="el-icon-plus" @click="addRole">添加角色</el-button>
        </div>
        <div>
            <el-collapse v-model="activeName" accordion style="width: 50%;margin-top: 10px" @change="change">
                <el-collapse-item title="一致性 Consistency" :name="r.id" v-for="(r,index) in roles" :key="index"
                                  :title="r.nameZh">
                    <el-card class="box-card">
                        <div slot="header" class="clearfix">
                            <span>可访问资源</span>
                            <el-button style="float: right; padding: 3px 0;color:#ff334b" type="text"
                                       icon="el-icon-delete" @click="deleteRole(r)"></el-button>
                        </div>
                        <div>
                            <el-tree
                                    node-key="id"
                                    ref="tree"
                                    :key="index"
                                    :default-checked-keys="selectedMenus"
                                    :data="allMenus" :props="defaultProps" show-checkbox
                            ></el-tree>
                            <div style="display: flex;justify-content: flex-end">
                                <el-button size="mini" @click="cancelUpdate(r.id)">取消修改</el-button>
                                <el-button size="mini" type="primary" @click="doUpdate(r.id,index)">确认修改</el-button>
                            </div>
                        </div>
                    </el-card>

                </el-collapse-item>
            </el-collapse>
        </div>
    </div>
</template>

<script>
    export default {
        name: "PermissMana",
        data() {
            return {
                   activeName:-1, //默认打开第几个collapse
                role: {
                    name: '',
                    nameZh: ''
                },
                roles: [],
                allMenus: [],
                selectedMenus: [],
                defaultProps: {
                    children: 'children',
                    label: 'name'  //这里要用资源的name属性，诞生defaultprops必须是label
                }
            }
        },
        mounted() {
            this.initPermiss();
        },
        methods: {
            addRole(){
                console.log(this.role.name)
                console.log(this.role.nameZh)
                if (this.role.name && this.role.nameZh){
                    this.postRequest("/system/basic/permiss/role",this.role).then(resp=>{
                        if (resp){
                            this.initPermiss();
                            this.role.name=''
                            this.role.nameZh=''
                        }
                    })
                }else {
                    this.$message.error("数据不可以为空！")
                }
            },
            deleteRole(role){
                this.$confirm('此操作将永久删除【' + role.name + '】角色, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/basic/permiss/" + role.id).then(resp => {
                        if (resp) {
                            this.initPermiss();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    })
                })
            },
            cancelUpdate(rid){
                this.activeName=-1;
                // this.initSelectedMenus(rid);
            },
            doUpdate(id, index) {
                let tree = this.$refs.tree[index];
                let selectedKey = tree.getCheckedKeys(true); //加true就只选择子叶
                let url = '/system/basic/permiss/?rid=' + id;
                selectedKey.forEach(key => {
                    url += '&mids=' + key;
                })
                this.putRequest(url).then(resp => {
                    if (resp) {
                        this.activeName=-1;
                        // this.initPermiss();
                    } else {
                    }
                })
            },
            change(rid) {
                if (rid) {
                    this.initAllMenus();
                    this.initSelectedMenus(rid);
                }
            },
            initSelectedMenus(rid) {
                this.getRequest("/system/basic/permiss/mids/" + rid).then(resp => {
                    if (resp) {
                        this.selectedMenus = resp;
                    }
                })
            },
            initAllMenus() {
                this.getRequest("/system/basic/permiss/menus").then(resp => {
                    if (resp) {
                        this.allMenus = resp;
                    }
                })
            },
            initPermiss() {
                this.getRequest("/system/basic/permiss/").then(resp => {
                    if (resp) {
                        this.roles = resp;
                    }
                });
            }
        }
    }
</script>

