<template>
    <div>
        <div>
            <el-input v-model="searchMsg" placeholder="输入部门名称搜索部门..." size="small" prefix-icon="el-icon-search"
                      style="width: 60%" @keydown.enter.native="doSearch"></el-input>
        </div>
        <div>
            <el-tree :data="deps" :props="defaultProps"
                     style="width: 55%;margin-top: 10px" size="small"
                     :filter-node-method="filterNode"
                     :expand-on-click-node="false"
                     ref="tree">
                <!--                在tree里面每一行加东西-->
                <span class="custom-tree-node" slot-scope="{ node, data }"
                      style="display:flex;justify-content: space-between;width: 100%">
        <span>{{ node.label }}</span>
        <span>
          <el-button
                  type="primary"
                  size="mini"
                  style="padding: 2px"
                  @click="() => addDepView(node,data)">
            添加部门
          </el-button>
          <el-button
                  type="danger"
                  size="mini"
                  style="padding: 2px"
                  @click="() => deleteDepView(data)">
            删除部门
          </el-button>
        </span>
      </span>
            </el-tree>
        </div>
        <el-dialog
                title="添加部门"
                :visible.sync="dialogVisible"
                width="30%">
            <div>
                <div>
                    <el-tag>上级部门</el-tag>
                    <span style="margin-left: 8px">{{pname}}</span>
                </div>
                <div style="margin-top: 8px">
                    <el-tag>部门名称</el-tag>
                    <el-input v-model="dep.name" placeholder="请输入部门名称..."
                              style="margin-left: 8px;width: 50%"></el-input>
                </div>
            </div>
            <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="doAddDep">确 定</el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "DepMana",
        data() {
            return {
                dialogVisible: false,
                searchMsg: '',
                dep: {
                    name: '',
                    parentId: -1
                },
                pname: '',
                defaultProps: {
                    children: 'children',
                    label: 'name'
                },
                deps: [],
            }
        },
        mounted() {
            this.initDeps()
        },
        methods: {
            addDep2Deps(deps, dep) {
                for (let i = 0; i < deps.len; i++) {
                    let d = deps[i]
                    if (d.id == dep.parentId) {
                        d.children = d.children.concat(dep);
                        return;
                    } else {
                        this.addDep2Deps(d.children, dep)
                    }
                }
            },
            initDep() {
                this.dep = {
                    name: '',
                    parentId: -1
                },
                    this.pname = ''
            },
            doAddDep() {
                this.postRequest("/system/basic/department/", this.dep).then(resp => {
                    if (resp) {
                        this.addDep2Deps(this.deps, resp.obj)    //出bug了，添加之后并没有自动展开
                        this.initDeps()
                        this.dialogVisible = false
                        this.initDep()
                    }
                })
            },
            removeDepFromDeps(deps,id){
                for (let i = 0; i <deps.length; i++) {
                    let d=deps[i]
                    if(d.id==id){
                        deps.splice(i,1)
                        return
                    }else {
                        this.removeDepFromDeps(d.children,id)
                    }
                }
            },
            deleteDepView(data) {
                this.$confirm('此操作将永久删除【'+data.name+'】部门, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                  this.deleteRequest("/system/basic/department/"+data.id).then(resp=>{
                      if (resp){
                          this.removeDepFromDeps(this.deps,data.id)
                      }
                  })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            addDepView(node, data) {
                this.pname = data.name
                this.dep.parentId = data.id
                this.dialogVisible = true;
            }
            ,
            initDeps() {
                this.getRequest("/system/basic/department/").then(resp => {
                    if (resp) {
                        this.deps = resp;
                    }
                })
            },
            doSearch() {
                this.searchMsg = "hello world!"
            },
            filterNode(value, data) { //value即filter传递过来的val参数，data就是json对象
                if (!value) {
                    return true; //则当前的data留着
                }
                return data.name.indexOf(value) != -1;
            }
        },
        watch: {
            searchMsg(val) {  //监控上面的searchMsg属性，一旦发生改变，则触发此方法 val参数即searchMsg的最新值
                this.$refs.tree.filter(val) //此filter方法即上面配的filterNode方法
            },
        }
    }
</script>

<style scoped>

</style>