<template>
    <div>
        <div style="margin-top: 10px">
            <el-input v-model="keyWords" placeholder="默认展示部分用户，可以通过用户名搜索更多用户..." size="small"
                      style="margin-left: 25%; width: 35%" prefix-icon="el-icon-search"
                      @keydown.enter.native="doSearch"
            ></el-input>
            <el-button type="primary" size="small" style="margin-left: 10px" icon="el-icon-search" @click="doSearch">搜索</el-button>
        </div>
        <div class="hr-container">
            <el-card class="box-card" v-for="(hr,index) in hrs" :key="index">
                <div slot="header" class="clearfix">
                    <span>{{hr.name}}</span>
                    <el-button style="float: right; padding: 3px 0;color: #ff334b" type="text" icon="el-icon-delete" @click="deleteHr(hr)">
                    </el-button>
                </div>
                <div>
                    <div class="img-container">
                        <img :src="hr.userface" class="image">
                    </div>
                    <div style="margin-top: 10px;color: #0893ff">
                        <div> 用户名：{{hr.name}}</div>
                        <div> 手机号码：{{hr.phone}}</div>
                        <div> 地址：{{hr.address}}</div>
                        <div> 用户状态：
                            <el-switch
                                    v-model="hr.enabled"
                                    active-color="#13ce66"
                                    inactive-color="#ff4949"
                                    @change="enabledChange(hr)"
                                    active-text="启用"
                                    inactive-text="禁用">
                            </el-switch>
                        </div>
                        <div> 用户角色：
                            <el-tag type="success" v-for="(role,index) in hr.roles" size="mini"
                                    style="margin-left: 4px">
                                {{role.nameZh}}
                            </el-tag>
                            <el-popover
                                    placement="right"
                                    title="角色列表"
                                    width="200"
                                    @show="showPop(hr)"
                                    @hide="hidePop(hr)"
                                    trigger="click"
                            >
                                <el-select v-model="selectedRoles" multiple placeholder="请选择">
                                    <el-option
                                            v-for="(r,indexj) in allRoles"
                                            :key="indexj"
                                            :label="r.nameZh"
                                            :value="r.id">
                                    </el-option>
                                </el-select>
                                <el-button slot="reference" type="text" icon="el-icon-more"></el-button>
                            </el-popover>
                        </div>
                        <div> 备注：{{hr.remark}}</div>
                    </div>
                </div>
            </el-card>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SysHr",
        data() {
            return {
                keyWords: '',
                hrs: [],
                selectedRoles: [],
                allRoles: []
            }
        },
        mounted() {
            this.initHr()
        },
        methods: {
            deleteHr(hr){
                this.$confirm('此操作将永久删除【'+hr.name+'】操作员, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/hr/"+hr.id).then(resp=>{
                        if (resp){
                            this.initHr()
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            hidePop(hr) {
                let roles = hr.roles
                let flag = false
                if (roles.length != this.selectedRoles.length) {
                    flag = true
                } else {
                    for (let i = 0; i <roles.length ; i++) {
                        let role=roles[i]
                        for (let j = 0; j <this.selectedRoles.length ; j++) {
                            if (role.id==this.selectedRoles[j]){
                                flag=false
                                break
                            }
                            flag=true
                        }
                        if (flag){
                            break
                        }
                    }
                }
                if (flag) {
                    let url = "/system/hr/role?hrid=" + hr.id;
                    this.selectedRoles.forEach(sr => {
                        url += '&rids=' + sr;
                    })
                    this.putRequest(url).then(resp => {
                        if (resp) {
                            this.initHr()
                        }
                    })
                }

            },
            doSearch(){
                this.initHr()
            },
            showPop(hr) {
                let roles = hr.roles
                this.selectedRoles = [];
                roles.forEach(role => {
                    this.selectedRoles.push(role.id)
                })
                this.initAllRoles();
            },
            enabledChange(hr) {
                delete hr.roles
                this.putRequest("/system/hr/", hr).then(resp => {
                })
            },
            initAllRoles() {
                this.getRequest("/system/hr/role").then(resp => {
                    if (resp) {
                        this.allRoles = resp;
                    }
                })
            },
            initHr() {
                this.getRequest("/system/hr?keyWords="+this.keyWords).then(resp => {
                    if (resp) {
                        this.hrs = resp;
                    }
                })
            }
        }
    }
</script>

<style scoped>
    .hr-container {
        margin-top: 10px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
    }

    .box-card {
        width: 30%;
        margin-top: 20px;
    }

    .img-container {
        display: flex;
        justify-content: center;
    }

    .image {
        width: 72px;
        height: 72px;
        border-radius: 72px;
    }
</style>