<template>
    <div>
        <div>
            <el-input
                    class="addPostInput"
                    placeholder="添加职称..."
                    prefix-icon="el-icon-plus"
                    size="mini"
                    v-model="jl.name"
            >
            </el-input>
            <el-select v-model="jl.titleLevel" placeholder="职称等级" size="mini">
                <el-option
                        v-for="item in jlLevels"
                        :key="item"
                        :label="item"
                        :value="item">
                </el-option>
            </el-select>
            <el-button icon="el-icon-plus" style="margin-left: 5px" size="mini" type="primary" @click="addJobLevel">添加
            </el-button>
        </div>
        <div class="JobLevelList">
            <el-table
                    :data="JobLevels"
                    stripe
                    @selection-change="handleSelectionChange"
                    border
                    size="small"
                    style="width: 80%">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="id"
                        label="编号"
                        width="55">
                </el-table-column>
                <el-table-column
                        prop="name"
                        label="职称名称"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="titleLevel"
                        label="职称级别"
                        width="180">
                </el-table-column>
                <el-table-column
                        prop="createDate"
                        label="创建时间"
                        width="150">
                </el-table-column>
                <el-table-column
                        label="是否启用"
                width="80">
                    <template slot-scope="scope">
                        <el-tag type="success" size="small" v-if="scope.row.enabled">已启用</el-tag>
                        <el-tag type="danger" size="small" v-else>未启用</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                                size="mini"
                                @click="showEditView(scope.$index, scope.row)">编辑
                        </el-button>
                        <!--scope.row是返回那一行的json-->
                        <el-button
                                size="mini"
                                type="danger"
                                @click="handleDelete(scope.$index, scope.row)">删除
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-button type="danger" size="small" style="margin-top: 8px" @click="deleteJLs"
                       :disabled="multipleSelections.length==0">批量删除
            </el-button>
        </div>
        <el-dialog
                title="修改职称信息"
                :visible.sync="dialogVisible"
                width="30%">
            <div>
                <el-tag>职称名称</el-tag>
                <el-input style="width: 150px;margin-left: 8px" size="small" v-model="updateJl.name"></el-input>
                <el-select v-model="updateJl.titleLevel" placeholder="职称等级" size="small" style="width: 100px;margin-left: 8px">
                    <el-option
                            v-for="item in jlLevels"
                            :key="item"
                            :label="item"
                            :value="item">
                    </el-option>
                </el-select>
            </div>
            <div>
                <el-tag>是否启用</el-tag>
                <el-switch
                        v-model="updateJl.enabled"
                        active-text="启用"
                        inactive-text="禁用"
                ></el-switch>
            </div>
            <span slot="footer">
    <el-button size="small" @click="dialogVisible = false">取 消</el-button>
    <el-button size="small" type="primary" @click="doUpdate" >确 定</el-button>
            </span></el-dialog>
    </div>
</template>

<script>
    export default {
        name: "JobLevel",
        data() {
            return {
                jl: {
                    name: '',
                    titleLevel: '',// 这里传过去的属性一定要跟后台接口给的属性名字一致！！！
                },
                updateJl: {
                    name: '',
                    titleLevel: '',
                    enabled:null
                },
                jlLevels: ['员级', '初级', '中级', '副高级', '正高级'],
                JobLevels: [],
                multipleSelections: [],
                dialogVisible: false
            }
        },
        mounted() {
            this.initJobLevel();
        },
        methods: {
            handleSelectionChange(val) {
                this.multipleSelections = val;
            },
            showEditView(index, data) {
                Object.assign(this.updateJl, data)
                this.dialogVisible = true;
            },
            doUpdate() {
                this.putRequest("/system/basic/joblevel/", this.updateJl).then(resp => {
                    if (resp) {
                        this.initJobLevel();
                        this.dialogVisible=false;
                    }
                })
            },
            handleDelete(index, data) {
                this.$confirm('此操作将永久删除【' + data.name + '】职称, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/system/basic/joblevel/" + data.id).then(resp => {
                        if (resp) {
                            this.initJobLevel();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    })
                })
            },
            initJobLevel() {
                this.getRequest("/system/basic/joblevel/").then(resp => {
                    if (resp) {
                        this.JobLevels = resp;
                        this.jl={
                            name:'',
                            titleLevel:''
                        }
                    }
                })
            },
            addJobLevel() {
                if (this.jl.name && this.jl.titleLevel) {
                    this.postRequest("/system/basic/joblevel/", this.jl).then(resp => {
                        if (resp) {
                            this.initJobLevel();
                        }
                    })
                }else {
                    this.$message.error("添加字段不可为空！")
                }
            },
            deleteJLs() {
                this.$confirm('此操作将永久删除【' + this.multipleSelections.length + '】条记录, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let ids = '?';
                    this.multipleSelections.forEach(item => {
                        ids += 'ids=' + item.id + '&';
                    })
                    this.deleteRequest("/system/basic/joblevel/" + ids).then(resp => {
                        if (resp) {
                            this.initJobLevel();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    })
                })
            },
        }
    }
</script>

<style>
    .JobLevelList {
        margin-top: 8px;
    }
</style>